import React, { Component } from 'react';
import './Article.scss';

import $ from 'jquery';

import anime from 'animejs';

class Article extends Component {

     constructor(props) {
            super(props);

            this.color = `#fff`;
            
            this.state = {
                color : `#fff`
            };
     }

    componentDidMount() {
         let img = $(this.ref).find('img');

         $(img).on('load', () => {
            let v = new Vibrant(img[0], 5);
            let swatches = v.swatches();

            if(!swatches || !swatches.Vibrant) return;

             this.setState({color : swatches.Vibrant.getHex()});
         });
    }

    render() {
         let {article, selectArticle, selectedArticle} = this.props;
         let {color} = this.state;

         let selectedClass = ``;

         if(selectedArticle === article.id) {
             selectedClass = ` selected`;
         }

          return (
               <div
                   className={`article-container${selectedClass}`}
                   ref = {r => this.ref = r}
                   onClick={() => selectArticle(article.id)}
               >
                   <div
                       className={`shadow`}
                       style={{backgroundColor : color}}
                   />
                   <div className={`img`} >
                       <img src={article.thumb}/>
                   </div>

               </div>
          );
      
     }
 
}

export default Article;