import React, { Component } from 'react';
import './ContactSection.scss';

class ContactSection extends Component {

     constructor(props) {
            super(props);
            
            this.state = {
                contactLinks : [
                    {
                        key : `github`,
                        name : `github`,
                        url : `https://github.com/mariusgrafu`
                    },
                    {
                        key : `linkedin`,
                        name : `linkedin`,
                        url : `https://www.linkedin.com/in/mariusgrafu/`
                    },
                    {
                        key : `puffysticks`,
                        name : `puffysticks`,
                        url : `https://puffysticks.com/`
                    },
                ]
            };
     }

     get contactLinks(){
         let {contactLinks} = this.state;

         return contactLinks.map((link) => {
            return (
                <a
                    key={link.key}
                    href={link.url}
                    target={`_blank`}
                    className={`contact-link ${link.key}`}
                >
                    {link.name}
                </a>
            );
         });
    }
 
     render() {
     
          return (
               <section className={`contact-section`}>

                    <div className="message">
                       <h1>marius grafu</h1>
                        <p>web developer/designer</p>
                    </div>

                   <div className="contact-links">
                       {this.contactLinks}
                   </div>

               </section>
          );
      
     }
 
}

export default ContactSection;