import React, { Component } from 'react';
import './ArticlePage.scss';
import ArticleService from "../../ArticleService";

import ReactMarkdown from 'react-markdown';

import anime from 'animejs';

class ArticlePage extends Component {

     constructor(props) {
            super(props);

            this.articleService = ArticleService.instance;
            
            this.state = {
                article : null
            };
     }

     componentWillMount() {
         let {goBack, articleId} = this.props;
         let article = this.articleService.getArticleById(articleId);

         if(!article) {
             goBack();
         } else {
             this.setState({article});
         }
     }

     get description() {
         let {article} = this.state;

         if(article.description) {
             return (
                 <div
                     className={`article-description`}
                     dangerouslySetInnerHTML={{__html : article.description}}
                 />
             );
         }

         return (<></>);
     }

     get githubUrl() {
         let {article} = this.state;

         if(!article.githubUrl) return (<></>);

         return (
             <a
                 className={`btn primary`}
                 href={article.githubUrl}
                 target={`_blank`}
             >
                 Github Project
             </a>
         );
     }

     get demoBtn() {
         let {article} = this.state;

         if(!article.demo) return (<></>);

         return (
             <a
                 className={`btn`}
                 href={article.demo}
                 target={`_blank`}
             >
                 Demo
             </a>
         );
     }

    render() {
         let {article} = this.state;
         let {goBack} = this.props;
     
          return (
              <div className={`article-page-container`}>
               <div className={`main-wrap article-page`}>
                   <div className={`left-section`}>

                       <div className={`sticky`}>
                           <div className="sticky-top">

                               <h1 className={`article-title`}>
                                   {article.name}
                               </h1>

                               {this.description}

                               <div className={`article-btns`}>

                                   {this.githubUrl}
                                   {this.demoBtn}
                                   <div
                                       className="btn"
                                       onClick={goBack}
                                   >
                                       go back
                                   </div>

                               </div>

                           </div>

                       </div>

                   </div>

                   <div className={`right-section`}>

                       <div className={`article-content`}>
                           <ReactMarkdown>
                               {article.content}
                           </ReactMarkdown>
                       </div>

                   </div>
               </div>
              </div>
          );
      
     }
 
}

export default ArticlePage;