import React, { Component } from 'react';
import './PortfolioSection.scss';

import ArticleService from '../../ArticleService';
import Article from "./Article/Article";

import anime from 'animejs';

class PortfolioSection extends Component {

     constructor(props) {
            super(props);

            this.articleService = ArticleService.instance;
            
            this.state = {
                articles : this.articleService.articles
            };
     }

     get articles() {
         let {articles} = this.state;
         let {selectArticle, selectedArticle} = this.props;

         return articles.map((article, i) => {
             return (
                 <Article
                     key={`${article.id}-${i}`}
                     article={article}
                     selectArticle = {selectArticle}
                     selectedArticle = {selectedArticle}
                 />
             );
         });
     }

     loadArticles = () => {
         let {selectedType} = this.props;

         if(!selectedType) {
             this.setState({articles : this.articleService.articles});
             return;
         }

         let articles = this.articleService.articles.filter((article) => {
            return (article.type.indexOf(selectedType) !== -1);
         });

         this.setState({articles});
     }

     componentWillMount() {
         this.loadArticles();
     }

    componentDidMount() {
         anime.remove(`.article-container`);
         anime({
             targets : `.article-container`,
             keyframes : [
                 {opacity: 0, translateY : -15},
                 {opacity: 1, translateY : 0}
             ],
             duration : 200,
             elasticity : 500,
             delay : anime.stagger(60, {grid: [5, 6], from: 'center'})
         })
     }

    render() {
     
          return (
               <section className={`portfolio-section`}>
                   <div className="articles-container">
                       {this.articles}
                   </div>
               </section>
          );
      
     }
 
}

export default PortfolioSection;